* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

#search-box {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
}

ul {
    list-style: none;
    padding: 0;
    font-size: 18px;
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    padding-left: 50px;
    padding-right: 50px;
    justify-content: space-between;
    margin: 0 auto;
}

form {
    color: #fff;
}

.header {
    background-color: #0349fc;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
}

.info {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
}

.preloader {
    display: flex;
    justify-content: center;
}

svg {
    width: 100px;
    height: 100px;
    display: inline-block;
}

.photo-card {
    margin-bottom: 15px;
}

.back-to-top {
    position: fixed;
    width: 32px;
    height: 32px;
    right: 7px;
    bottom: 15px;
    border: none;
    cursor: pointer;
    opacity: 1;
    border-radius: 50%;
    background-image: url("../images/up.png");
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 1s ease;
}

.back-to-top:hover {
    opacity: 0.6;
}

.hidden {
    opacity: 0;
}

.search-btn {
    background-image: url("../images/search.png");
    background-repeat: no-repeat;
    background-size: cover;
    height: 15px;
    width: 15px;
}

input {
    border: none;
    outline: none;
    width: 300px;
    border-radius: 10px;
    padding: 5 10px;
}


input::placeholder {
    color: rgb(7, 47, 47);
}

button {
    margin: 0;
    border: 0;
    border-top-right-radius: 50%;
    cursor: pointer;
    background-color: #fff;
}

.input-block {
    background-color: #fff;
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 5px;
}